<template>
  <div class="container">
    <div class="header">
      <h1>글 작성하기</h1>
    </div>
    <form @submit.prevent="handleSubmit">
      <div class="form-group">
        <input
          class="form-content"
          type="text"
          id="title"
          v-model="form.title"
          placeholder="제목을 입력하세요"
          required
          maxlength="29"
        />
      </div>
      <br>
      <div class="form-group">
        <el-tiptap
          :content="content"
          :extensions="extensions"
          :enable-char-count="false"
          lang="en"
          placeholder="Write something..."
          height="500px"
          @onUpdate="onEditorUpdate"
        />
      </div>
      <div class="form-actions">
        <button type="submit">저장</button>
        <button type="button" @click="cancel">취소</button>
      </div>
    </form>
  </div>
</template>

<script>
import { reactive, ref } from 'vue';
import axios from 'axios';
import { useRouter } from 'vue-router';
import css from 'highlight.js/lib/languages/css';
import js from 'highlight.js/lib/languages/javascript';
import ts from 'highlight.js/lib/languages/typescript';
import html from 'highlight.js/lib/languages/xml';
// load all highlight.js languages
import { lowlight } from 'lowlight';
import {
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  Underline,
  Italic,
  TextAlign,
  FontSize,
  codeView,
  Image,
  Strike,
  BulletList,
  OrderedList,
  Table,
  Link,
  Iframe,
  CodeBlockLowlight,
  Blockquote,
  TaskList,
  Indent,
  LineHeight,
  HorizontalRule,
  HardBreak,
  History,
  FormatClear,
  Color,
  Highlight,
  Fullscreen,
  SelectAll,
  FontFamily,
  Gapcursor,
  Commands,
  getSuggestionItems,
  renderItems,
} from 'element-tiptap-vue3-fixed';

lowlight.registerLanguage('html', html);
lowlight.registerLanguage('css', css);
lowlight.registerLanguage('js', js);
lowlight.registerLanguage('ts', ts);

export default {
  name: "WritePostPage",
  
  setup() {
    const content = ref('');
    const extensions = reactive([
      Doc,
      Text,
      Paragraph,
      Bold,
      Underline.configure({ bubble: true }),
      Italic.configure({ bubble: true }),
      TextAlign.configure({ bubble: true }),
      FontSize,
      Heading,
      codeView,
      Image,
      Strike,
      BulletList,
      OrderedList,
      Table.configure({ resizable: true }),
      Link.configure({ bubble: true }),
      Iframe,
      CodeBlockLowlight.configure({ lowlight }),
      Blockquote,
      TaskList,
      Indent,
      LineHeight,
      HorizontalRule,
      HardBreak,
      History,
      FormatClear,
      Color,
      Highlight,
      Fullscreen,
      SelectAll,
      FontFamily,
      Gapcursor,
      Commands.configure({
        suggestion: {
          items: getSuggestionItems,
          render: renderItems,
        },
      }),
    ]);

    const onEditorUpdate = (val) => {
      content.value = val;
    };

    const saveContent = () => {
      console.log('Saved content:', content.value);
    };

    const form = reactive({
      title: "",
      content: "",
    });
    const router = useRouter();

    // 글 작성 처리 함수
    const handleSubmit = () => {
      try {


        if (!content.value || content.value.trim() === "") {
          alert("내용을 입력해주세요.");
          return;
        }
        axios.post('/api/post/upload', {
          token: localStorage.getItem('jwtToken'),
          title: form.title,
          content: content.value,
          date: "dummy",
        })
        .then(() => {
          alert("글이 저장되었습니다!");
          router.push('/PostTable');
        })
        .catch((error) => {
          console.error("Error during post submission:", error);
          alert('글 저장에 실패했습니다.');
        });
      } catch (error) {
        console.error("Unexpected error:", error);
        alert('글 저장에 실패했습니다.');
      }

      // 폼 초기화
      form.title = "";
      form.content = "";
    };

    

    // 취소 함수 (메인 페이지로 이동)
    const cancel = () => {
      if (confirm("작성을 취소하시겠습니까? 작성한 내용은 사라집니다.")) {
        form.title = "";
        form.content = "";
        router.push("/PostTable");
      }
    };

    return {
      content,
      extensions,
      onEditorUpdate,
      saveContent,
      form,
      handleSubmit,
      cancel,
    };
  },
};
</script>

