<template>
  <div class="carousel-container">
    <!-- Flicking 캐러셀 -->
    <flicking ref="flicking" :options="{ circular: false }">
      <div class="panel">첫 번째 패널</div>
      <div class="panel">두 번째 패널</div>
      <div class="panel">세 번째 패널</div>
      <div class="panel">네 번째 패널</div>
    </flicking>

    <!-- 화살표 버튼 -->
    <!-- <button class="flicking-arrow-prev">◀</button>
    <button class="flicking-arrow-next">▶</button> -->
  </div>
</template>

<script>
import { defineComponent, ref, nextTick } from "vue";
import Flicking from "@egjs/vue3-flicking";
import "@egjs/vue3-flicking/dist/flicking.css";
import { Arrow } from "@egjs/flicking-plugins";

export default defineComponent({
  name : "FlickingExample",
    components: { Flicking },
  setup() {
    const flicking = ref(null);
    const plugins = ref([]);

    nextTick(() => {
      plugins.value = [
        new Arrow({
          parentEl: ".carousel-container",
          prevEl: ".flicking-arrow-prev",
          nextEl: ".flicking-arrow-next",
        }),
      ];
    });

    return { flicking, plugins };
  },
});
</script>

<style>
.carousel-container {
  position: relative;
  width: 300px;
  margin: auto;
  text-align: center;
}

.panel {
  width: 100%;
  padding: 20px;
  font-size: 20px;
  text-align: center;
  white-space: nowrap;
}

.flicking-arrow-prev,
.flicking-arrow-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  font-size: 24px;
  z-index: 10;
}

.flicking-arrow-prev {
  left: 10px;
}

.flicking-arrow-next {
  right: 10px;
}
</style>