<template>
  <div class="container">
    <div class="header">
      <h1>게시판</h1>
    </div>
    <table class="post-list">
      <thead>
        <tr>
          <th>제목</th>
          <th>작성자</th>
          <th>작성일</th>
        </tr>
      </thead>
      <tbody>
        <tr class="post-item" v-for="post in posts" :key="post.id">
          <td><a :href="'/post/' + post.id">{{ post.title }}</a></td>
          <td>{{ post.userId }}</td>
          <td v-if="post.createdAtDate  == today">
                {{post.createdAtTime.substring(0,5)}}
          </td>
          <td v-if= "post.createdAtDate  !== today">
                  {{post.createdAtDate}}
            </td>
            
        </tr>
      </tbody>
    </table>
    <div class="write-button">
      <a href="/write">글쓰기</a>
    </div>
  </div>
</template>

<script>
// import { reactive } from "vue";
// import { onMounted } from "vue";
// import { useRoute } from "vue-router";
import { ref } from "vue";
import axios from "axios"


// const route = useRoute();
const userEmail = ref('');
const userId = ref('');
const posts = ref([]);
export default {
  name: "BoardMainPage",
  setup() {
    axios.get("/api/post/getPost").then((res) => {
      console.log(res.data);
      posts.value = res.data;
    });

    const today = new Date().toLocaleDateString('ko-KR', {
      year: 'numeric',
      month: '2-digit', 
      day: '2-digit'
    }).split('. ').join('-').replace(/\.$/, ''); // YYYY-MM-DD 형식
    console.log(today);

    return {
      today,
      userEmail,
      userId,
      posts,
    };
    
  },
};
</script>

