<template>
    <div class="user-info">
      <h2>회원 정보</h2>
      <p>-----------</p>
      <div v-if="info">
        <p><strong>아이디:</strong> {{ info.userId }}</p>
        <p><strong>이메일:</strong> {{ info.userEmail }}</p>


      </div>
      <div v-else>
        <p>회원 정보를 불러오는 중입니다...</p>
      </div>
      
      <br>
      
      <button class="button" @click="fetchUserInfo">정보 새로고침</button>
    <br>
    <br>
    <br>
    <table class="post-list">
      <thead>
        <tr>
          <th>제목</th>
          <th>작성일</th>
        </tr>
      </thead>
      <tbody>
        <tr class="post-item" v-for="post in info.posts" :key="post.id">
          <td><a :href="'/post/' + post.id">{{ post.title }}</a></td>
          <td>{{ post.createdAtDate }}</td>
        </tr>
      </tbody>
    </table>
   
    </div>
    
</template>
  
  <script>
  import { ref } from "vue";
  import axios from "axios";
  
  export default {
    name: "UserInfo",
    setup() {

      const info = ref({
          userId: null,
          userEmail: null,
          posts: null,
      });

      const fetchUserInfo = () => {
        

        axios.get("/api/info/all", {
            headers: {
              jwtToken: `Bearer ${localStorage.getItem("jwtToken")}`,
            },
          })
          .then((response) => {
            console.log(response.data)
            info.value = response.data;

          })
          .catch((error) => {

            console.error(error);
          });

      };
      


  
      const formatDate = (dateString) => {
        const options = { year: "numeric", month: "long", day: "numeric" };
        return new Date(dateString).toLocaleDateString("ko-KR", options);
      };
  
      // 컴포넌트가 로드되면 데이터를 가져옴
      fetchUserInfo();
  
      return {
          info,
        fetchUserInfo,
        formatDate,
      };
    },
  };
  </script>
  
