    const handleLogin = async () => {
      try {
        //http://localhost:8080/refer/getUser?userEmail:B@example.com&userId:user2&password:user2
          axios.post('/api/auth/login', {
              userId: id.value,
              password: password.value,
          })
              .then(response => {
                  const token = response.data.token;
                  console.log('✅ 로그인 성공! 토큰:', token);

                  // 예: 로컬스토리지에 저장
                  localStorage.setItem('jwtToken', token);
                  router.push({ path: "/PostTable" });
                  alert("로그인 성공!");

              })
              .catch(error => {
                  console.error('❌ 로그인 실패:', error.response?.data || error.message);
              });
        // const response = await axios.post("/api/login/login", {
        //   userEmail: userEmail.value,
        //   password: password.value,
        // });
        //
        // if (response.status >= 200 && response.status < 300) {
        //   // 사용자 ID 조회
        //   const userIdResponse = await axios.get(`/api/refer/getId/email/${userEmail.value}`);
        //
        //   // 사용자 ID를 로컬 스토리지에 저장
        //   const userId = userIdResponse.data.userId;
        //   const email = userEmail.value;
        //
        //   localStorage.setItem("userId", userId);
        //   localStorage.setItem("pwd", password.value);
        //   localStorage.setItem("userEmail", email);

        //
        //   // 로그인 성공 후 이동
        //   router.push({ path: "/PostTable" });
        //   alert("로그인 성공!");
        // }
      } catch (error) {
        console.error("로그인 오류:", error);
        alert("이메일 또는 비밀번호가 알맞지 않습니다.");
      }
    };

    // 컴포넌트가 마운트될 때 초기화
    onMounted(() => {
      // router.query로부터 값 할당
      id.value = router.currentRoute.value.query.id || "";
      password.value = router.currentRoute.value.query.password || "";

    });

    return {
      id,
      password,
      handleLogin,
    };
  },
};
</script>

