<template>
<body>
    <div class="container">
        <div class="post-detail">
            <h1 style="display: inline-block;">{{ data.title }}</h1>
            <router-link :to="`/modify/${id}`">
            <p v-if="modifyAuthority == true" style="float: right; display: inline-block;">[수정]</p>
            </router-link>
            <br>
            <p>---------------------------------<br>
                작성자: <strong>{{ data.userId }}</strong><br>
                작성일: <strong>{{ data.createdAtDate }}</strong><br>
            ---------------------------------</p>
            <p v-html="data.content"></p>
            <a href="/PostTable" class="button">돌아가기</a>
            <a v-if="modifyAuthority" href="/PostTable" class="del_btn" @click="deleteRequest">게시물 삭제</a>
        </div>
    </div>
</body>


</template>
<style>
         
        body {
            font-family: Arial, sans-serif;
            margin: 0;
            padding: 0;
            text-align:left;
        }
        .container {
            width: 90%;
            max-width: 800px;
            margin: 20px auto;
        }
        .post-detail {
            outline: solid;
            padding: 50px;
            border-radius: 10px;
        }
        .post-detail h1 {
            margin-top: 0;
        }
        .post-detail p {
            line-height: 1.6;
            margin-bottom: 20px;
        }

        .del_btn {
            padding: 10px 20px;
            background-color: #ff0000;
            color: rgb(255, 255, 255);
            border: none;
            border-radius: 5px;
            cursor: pointer;
            display: inline-block;
            text-decoration: none;
            font-size: 16px;
            transition: background-color 0.3s ease;
            float:right;
            }

            .del_btn:hover {
            background-color: #ececec;
            outline: 1px solid #ff0000;
            color: #ff0000
            }

        .button {
            display: inline-block;
            padding: 10px 20px;
            background-color: #007BFF; /* 파란색 버튼 */
            border: none;
            color : white;
            border-radius: 5px;
            text-decoration: none;
            font-size: 16px;
            cursor: pointer;
            transition: background-color 0.3s ease;
        }
        .button:hover {
            background-color: #0056b3; /* hover 시 진한 파란색 */
        }
    </style>
    <script>
    import axios from "axios";
    import { useRoute } from "vue-router"
    import { ref } from "vue";

    export default {
      name: "UserLogin",
      setup() {
            const modifyAuthority = ref(false);
            const route = useRoute();
            var data = ref({userId:null, date:null, content:null});

            axios.get(`/api/post/getPost/id/${route.params.id}`)
            .then(response => {
                console.log(response.data);
                data.value = response.data;
                axios.post("/api/protected/userId",null,
                    {
                        headers: {
                            Authorization: `Bearer ${localStorage.getItem("jwtToken")}`,
                            userId: data.value.userId,
                        }
                    }).then(response => { console.log(response.data)
                                            if(response.status === 202){ modifyAuthority.value = true;}})
                })
                    .catch(error => {
                        console.error(error);
                });


            const deleteRequest = () => {
                axios.delete(`/api/post/delete/id/${route.params.id}`)
            }
            const id = route.params.id

            return {
                data,
                modifyAuthority,
                deleteRequest,
                id,
                };
           
            
            
      }
    };
    </script>