<template>
  <div id="register">
    <h1>회원가입</h1>
    <form @submit.prevent="handleRegister">
      <input v-model="userEmail" placeholder="이메일" required />
      <input v-model="userId" placeholder="아이디" required />
      <input v-model="password" type="password" placeholder="비밀번호" required />
      <button type="submit">회원가입</button>
    </form>
    <p>이미 회원이신가요? <router-link to="/">로그인</router-link></p>
  </div>
</template>

<script>
import { ref } from "vue";
import axios from "axios";
import { useRouter } from "vue-router";

export default {
  name: "UserRegister",
  setup() {
    const router = useRouter();

    const userId = ref("");
    const userEmail = ref("");
    const password = ref("");

    const isValidEmail = (email) => {
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      return emailRegex.test(email);
    };
    const submitForm = async () => {
      try {
        if (isValidEmail(userEmail.value) == false) {
          alert("올바른 Email 형식이 아닙니다.");
        } else if (isValidEmail(userEmail.value) == true)
        {
          // POST 요청 보내기
          axios.post('/api/auth/register', {
            userId: userId.value,
            userEmail: userEmail.value,
            password: password.value
          })
          .then(response => {
            console.log('✅ 회원가입 성공:', response.data);
            alert("회원가입이 완료되었습니다.");
            router.push({
              path: "/login",
              query: {
                 email: userEmail.value,
                 password: password.value
              },
            });
          })
          .catch(error => {
            if (error.response?.status === 409) {
              const cause = error.response.data.error;
              if (cause === "userId") {
                alert("이미 사용 중인 아이디입니다.");
              } else if (cause === "userEmail") {
                alert("이미 사용 중인 이메일입니다.");
              } else {
                alert("중복된 항목이 있습니다.");
              }
            } else {
              console.error('❌ 회원가입 실패:', error.response?.data || error.message);
              alert("회원가입 중 오류가 발생했습니다.");
            }
          });
          }

      } catch (error) {
        console.error("Error during API call:", error);
      }
    };

    const handleRegister = async () => {
      await submitForm();
    };

    return {
      userId,
      userEmail,
      password,
      handleRegister,
    };
  },
};
</script>

<style scoped>
#register {
  max-width: 300px;
  margin: 0 auto;
}

form {
  display: flex;
  flex-direction: column;
}

input {
  margin-bottom: 10px;
  padding: 10px;
}

button {
  padding: 10px;
  background-color: #4caf50;
  color: white;
  border: none;
  cursor: pointer;
}
</style>
