<template>
    <div id="app">


        <nav class="navbar navbar-expand-lg navbar-light bg-light" style="position: relative;">
            <i class="navbar-brand bi bi-person-circle user-icon" @click="toggleMenu" href="#"></i>
            <a class="nav-link" href="/gallery">Gallery</a>
            <a class="nav-link" href="/PostTable">Community</a>




        </nav>
        <div class="user-menu">

            <!-- 드롭다운 메뉴 -->
            <div v-if="menuOpen && isLogined" class="dropdown">
                <router-link to="/user/" class="menu-item" @click="toggleMenu">회원 페이지</router-link>
                <a @click="logout" class="menu-item">로그아웃</a>
            </div>
            <div v-if="menuOpen && !isLogined" class="dropdown">
                <router-link to="/Login" class="menu-item" @click="toggleMenu">로그인</router-link>
            </div>
        </div>

        <!-- 나머지 앱 콘텐츠 -->
        <router-view></router-view>
    </div>
</template>
<script>
import { ref, computed } from 'vue';
import { useStore } from 'vuex';

export default {
    name: "App",

    setup() {
        const menuOpen = ref(false);
        const store = useStore();

        // Define `isLogined` as a computed property
        const isLogined = computed(() => store.getters.isAuthenticated);

        // Setup logic in onMounted lifecycle hook

        // 드롭다운 메뉴 토글
        const toggleMenu = () => {
            menuOpen.value = !menuOpen.value;
        };

        // 로그아웃 (사용자 구현)
        const logout = () => {
            menuOpen.value = false;
            localStorage.removeItem('userId');
            localStorage.removeItem('pwd');
            localStorage.removeItem('userEmail');
            window.location.href = '/login';
        };

        return {
            userId : localStorage.getItem('userId'),
            isLogined,
            menuOpen,
            toggleMenu,
            logout,
        };
    },
};
</script>
